@import "../base/colors";
@import "../base/variables";
@import "../base/fonts";

// Typography
// Headings
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
.cover-card-heading, .cover-card-excerpt, .articles-block h2, .article-list-item-headline a {
  font-family: $roboto;
  font-weight: 700;
  margin-top:0;
  margin-bottom:.5rem;
  line-height:1.25;
}

h1, .h1 {
  font-family: $helvetica-extblk;
  font-size:calc(1.475rem + 1.5vw);
  margin-bottom: .7rem;

  @include large-and-up {
    font-size: $font-size-xxl;
  }

  @include xx-large-and-up {
    font-size: $font-size-xxxl;
  }
}

.h2, h2 {
  font-size:calc(1.35rem + .9vw);

  @include large-and-up {
    font-size: $font-size-xl;
  }

  @include xx-large-and-up {
    font-size: $font-size-xxl;
  }
}

h3, .h3 {
  font-size:calc(1.3rem + .6vw);

  @include large-and-up {
    font-size: $font-size-lg;
  }

  @include xx-large-and-up {
    font-size: $font-size-xl;
  }
}

h4, .h4 {
  font-size:calc(1.25rem + .3vw);

  @include large-and-up {
    font-size: $font-size-md;
  }

  @include xx-large-and-up {
    font-size: $font-size-lg;
  }
}

h5, .h5 {
  font-size: 1.2rem;
}

h6, .h6 {
  font-size: $font-size-sm;
}

div.cookies-intro>div.cookies-text {
  font-weight: 500!important;
}

div.carousel-captions-wrapper h2 {
  font-family: $helvetica-extbold!important;
  font-size: $font-size-xl;
}

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

