  color: $grey-70;
  font-weight: 400;
}

.page-header-content p {
  padding-left: 0;
  font-weight: 400;
}

.wp-block-file a::before {
  background-color: $dark-green;
}

p.logged-in-as a,
div.top-page-tags div.tag-wrap.issues a.tag-item.tag {
  color: $dark-green;

  &:hover {
    text-decoration: underline;
  }
}

.split-two-column-item-title a:hover {
  color: $yellow;
}

.split-two-column-item-tag, .split-two-column-item-link {
  color: $yellow !important;
}

.take-action-covers-block .cover .cover-card-content .cover-card-excerpt {
  font-family: $roboto!important;
  font-weight: 400;
}
.content-covers-block .content-covers-block-wrap .content-covers-block-info h5>a {
  color: $dark-green !important;
  font-family: $roboto;
  font-size: larger;
}

//on listing pages
div.query-list-item-post-terms div.taxonomy-p4-page-type a[rel="tag"],
div.wrapper-post-tag div.taxonomy-post_tag.wp-block-post-terms  a[rel="tag"],
div.query-list-item-body div.query-list-item-post-terms div.wrapper-post-term div.taxonomy-p4-page-type.wp-block-post-terms a
{
  font-weight: 400;
}

h4.query-list-item-headline.wp-block-post-title a {
  color: $black!important;
}

div.wrapper-post-tag ::before,
.query-list-item-body:has(.taxonomy-p4-page-type):has(.taxonomy-post_tag) .wrapper-post-tag:before {
  color: $dark-green;
}

//post info preview
div.article-list-item-tags.top-page-tags>a.tag-item.tag-item--main.page-type,
div.tag-wrap.tags>a.tag-item.tag, .single-post-time, a.comment-link, a.comment-link>span.quiantity, a.comment-link>span.display-text {
